<template>
<!-- 添加歌曲页面 -->
	<transition>
	<!-- @click.stop阻止冒泡 -->
		<div class="add-song" v-show="showFlag" @click.stop>
			<div class="header">
				<h1 class="title">添加歌曲</h1>
				<div class="close" @click="hide">
					<i class="icon-close"></i>
				</div>
			</div>
			<div class="search-box-wrapper">
			<!-- 为 search-box组件传一个placeholder 同时需要监听query事件-->
				<search-box ref="searchBox" @query="onQueryChange" placeholder="搜索歌曲"></search-box>
			</div>
			<div class="shortcut" v-show="!query">
				<switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
			</div>
			<div class="list-wrapper" v-show="!query">
				<scroll v-if="currentIndex === 0" :data="playHistory" class="list-scroll" ref="songList">
					<div class="inner-list">
						<song-list :songs="playHistory" @select="selectSong"></song-list>
					</div>
				</scroll>
				<scroll :refreshDelay="refreshDelay" v-if="currentIndex === 1" :data="playHistory" class="list-scroll" ref="searchList">
					<div class="inner-list">
						<search-list @delete="deleteSearchHistory" @select="addQuery" :searches="searchHistory"></search-list>
					</div>
				</scroll>
			</div>
			<div class="search-result" v-show="query">
				<suggest :query="query" :showSinger="showSinger" @select="selectSuggest" @listScroll="blurInput"></suggest>
			</div>
			<top-tip ref="topTip">
				<div class="tip-title">
					<i class="icon-ok"></i>
					<span class="text">1首歌曲已添加到播放列表</span>
				</div>
			</top-tip>
		</div>
	</transition>
</template>

<script type="text/ecmascript-6">
import SearchBox from 'base/search-box/search-box'
import Suggest from 'components/suggest/suggest'
import {searchMixin} from 'common/js/mixin'
import Switches from 'base/switches/switches'
import Scroll from 'base/scroll/scroll'
import {mapGetters, mapActions} from 'vuex'
import SongList from 'base/song-list/song-list'
import Song from 'common/js/song'
import SearchList from 'base/search-list/search-list'
import TopTip from 'base/top-tip/top-tip'
export default {
	mixins: [searchMixin],
	data() {
		return {
			// 组件的显示或隐藏
			showFlag: false,
			// 搜索词
			// query: '',
			// 是否搜索歌手
			showSinger: false,
			currentIndex: 0,
			switches: [
				{name: '最近播放'},
				{name: '搜索历史'}
			]
		}
	},
	computed: {
		...mapGetters([
			'playHistory'
		])
	},
	methods: {
		// 使组件显示
		show() {
			this.showFlag = true
			setTimeout(() => {
				if (this.currentIndex === 0) {
					this.$refs.songList.refresh()
				} else {
					this.$refs.searchList.refresh()
				}
			}, 20)
		},
		// 使组件隐藏
		hide() {
			this.showFlag = false
		},
		search(query) {
			this.query = query
		},
		// 保存子组件选择的那个item，存储在searchHistory
		selectSuggest() {
			this.saveSearchHistory(this.query)
			this.showTip()
		},
		// 切换导航按钮状态
		switchItem(index) {
			this.currentIndex = index
		},
		// 选择一首歌曲添加到播放列表
		selectSong(song, index) {
			if (index !== 0) {
				this.insertSong(new Song(song))
				this.showTip()
			}
		},
		// 选择成功提示
		showTip() {
			this.$refs.topTip.show()
		},
		...mapActions([
			'insertSong'
		])
	},
	components: {
		SearchBox,
		Suggest,
		Switches,
		Scroll,
		SongList,
		SearchList,
		TopTip
	}
}
	
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';
.add-song
	position: fixed
	top: 0
	left: 0
	right: 0
	bottom: 0
	width: 100%
	background: $color-background
	z-index: 200
	.header
		position: relative
		line-height: 45px
		text-align: center
		font-size: $font-size-large
		color: $color-text
		.close
			position: absolute
			top: 0
			right: 15px
			color: $color-theme
	.search-box-wrapper
		margin: 20px
	.shortcut
		position: relative
	.list-wrapper
		position: absolute
		top: 150px
		bottom: 0
		left: 0
		right: 0
		width: 100%
		padding: 0 30px
		.list-scroll
			height: 100%
			overflow: hidden
	.search-result
		position: fixed
		top: 124px
		bottom: 0
		width: 100%
</style>



